
.homeSlider{
	width: 100%;
	position: absolute;
	top: 0;
	left: 0;
}
.section{
	width: 100%;
	height: auto;
}
.homeSlider .full{
	width: 100%;
	height: auto;
	position: absolute;
	top: 140px;
}
.homeList{
	width: 100%;
	height: auto;
	padding-top: 16px;
}
.bgColor{
	background-color: white;
}
.title{
	width: 100%;
	height: 24px;
	line-height: 24px;
	padding-left: 10px;
	text-align: left;
	font-size: 16px;
	font-weight: 300;
	box-sizing: border-box;
}
.hotAction{
	border-left: 6px solid #ff8447;
}
.pubAction{
	border-left: 6px solid #42BD56;
}




