
header{
	width: 100%;
	height: 50px;
	line-height: 50px;
	position: fixed;
	left: 0;
	top: 0;
	background-color:#ffffff;
}
.headerColor{
	background-color:#42bd56;
}
.pageClass{
	color: white;
}
header .searchBox{
	margin: 7px 0 7px 10px;
	width: 80%;
	height: 36px;
	line-height: 36px;
	border-radius: 3px;
	background-color: white;
	overflow: hidden;
	position: absolute;
	top: 0;		
	color: #E4E4E4;
	font-weight: 300;
	font-size: 16px;
}
header .searchBox i:nth-of-type(1){
	position: absolute;
	width: 36px;
	height: 36px;
	line-height: 36px;
	left: 0;
	top: 0;
}
header .searchBox i:nth-of-type(2){
	position: absolute;
	width: 36px;
	height: 36px;
	line-height: 36px;	
	right: 0;
}
header .searchBox input{
	display: block;
	margin: 0;
	padding: 0;
	width: 100%;
	height: 36px;
	line-height: 36px;
	padding-left: 36px;
	box-sizing: border-box;
	outline: none;
	border: none;
}
header .login,header .search,header .user{
	width: 36px;
	height: 50px;
	margin-right: 8px;
	float: right;
	color: white;
	font-size: 18px;
	color: #42bd56;
}
header h3{
	position: absolute;
	width: 100px;
	height: 50px;
	line-height: 50px;
	left: 50%;
	margin-left: -50px;
	font-size: 18px;
	text-align: center;
}



