
footer {
  width: 100%;
  height: 52px;
  line-height: 52px;
  position: fixed;
  bottom: 0;
  color: #b2b2b2;
  border-top: 1px solid #e4e4e4;
  padding-top: 8px;
  box-sizing: border-box;
}
.footerColor {
  background-color: #ffffff;
}
.active {
  color: #42bd56;
}
footer ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
  overflow: hidden;
}
footer ul li {
  float: left;
  width: 20%;
  text-align: center;
}
footer ul a{
	display: block;
	width: 100%;
	height: 100%;
	overflow: hidden;
	clear: both;
	outline: none;
	text-decoration: none;
	border: none;
	color: #b2b2b2;
}
footer span {
  display: block;
  float: left;
  width: 100%;
  height: 24px;
  line-height: 24px;
  font-size: 14px;
}
footer li .fa{
  width: 100%;	
  float: left;
}



