
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  padding: 0;
  margin: 0;
  width: 100%;
	font: 12px/1.5 tahoma,"microsoft yahei","\5FAE\8F6F\96C5\9ED1"
}
h1,h2,h3,h4,p{
	margin: 0;
	padding: 0;
	text-align: left;
	font-weight: 300;
}
ul,ol,li,a{
	margin: 0;
	padding: 0;
	outline: none;
	border: none;
	text-decoration: none;
}
img{
	padding: 0;
	margin: 0;
	vertical-align: top;
	border: none;
}
header{
	width: 100%;
	height: 60px;
	position: absolute;
}
.content{
	width: 100%;
	position: absolute;
	top: 50px;
	bottom: 52px;
	overflow: hidden;
  background-color:#f7f7f7;
}
footer{
	width: 100%;
	height: 52px;
	position: absolute;
	bottom: 0;
}



