
.movieSlider{
	width: 100%;
	position: absolute;
	top: 0;
	left: 0;
}
.section{
	width: 100%;
	height: auto;
}
.full{
	width: 100%;
	height: auto;
	position: absolute;
	top: 30px;
	box-sizing: border-box;
}
.movieList{
	width: 100%;
	height: auto;
	margin-top: 12px;
}
.bgColor{
	background-color: white;
}
.movieSlider .movieTitle{
	width: 100%;
	height: 24px;
	line-height: 24px;
	text-align: left;
	font-size: 16px;
	font-weight: 300;
	box-sizing: border-box;
	padding-left: 10px;
}
.more{
	font-size: 12px;
	color: #42BD56;
	float: right;
	margin-right: 10px;
}
.titlePic{
	margin-top: -16px;
}
.titlePic img{
	width: 100%;
	height: 80px;
}
.titleP{
	height: 36px;
	line-height: 36px;
	font-size: 16px;
	font-weight: 500;
	padding-left: 16px;
}
.selectOption{
	padding-bottom: 20px;
	overflow: hidden;
}
.selectOption div{
	height: 26px;
	line-height: 26px;
	border: 1px solid #42BD56;
	float: left;
	margin: 16px 2px 0 10px;
	padding: 2px 14px;
	border-radius: 20px;
}
.goodTitle{
	box-sizing: border-box;
	padding-left: 10px;
	text-align: left;
	margin-bottom: 12px;
}
.goodList{
	width: 100%;
	height: 70px;
	margin-bottom: 8px;
	padding-left: 10px;
	text-align: left;
	box-sizing: border-box;
}
.goodList img{
	width: 50px;
	height: 50px;
	display: block;
	float: right;
	margin: 5px 10px;
}
.goodList h3{
	margin-bottom: 6px;
}




